* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montserrat", sans-serif;
  background-image: url("/src/images/bg.jpg");
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.btn {
  width: 100%;
  margin: 10px;
  max-width: 70px;
  padding: 8px 15px;
  border-radius: 17px 17px 17px 17px;
  outline-color: whitesmoke;
  color: #313131;
  font-size: 15px;
  font-weight: 300;
  background-color: rgba(255, 255, 255, 0.76);
  transition: 0.1s ease-in-out;
}

.btn :hover {
  background-color: rgba(255, 255, 255, 0.9);
}

.app-wrap {
  /* border-style: solid; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 50px 15px 15px; */
  box-shadow: whitesmoke;
}

.search-box {
  width: 100%;
  /* max-width: 500px; */
  padding: 10px 15px;
  border: none;
  outline: none;
  border-radius: 17px 17px 17px 17px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #313131;
  font-size: 16px;
  font-weight: 300;
  transition: 0.1s ease-in-out;
}

header input:hover {
  background-color: rgba(255, 255, 255, 0.9);
}

/* main {
  display: flex;
  flex-direction: column;
  padding: 25px;
  text-align: center;
  align-items: center;
} */

.location .city {
  color: #fff;
  font-size: 80px;
  font-weight: 500;
  margin-bottom: 5px;
}

.location .date {
  color: #fff;
  font-size: 16px;
}

.current .temp {
  color: #fff;
  font-size: 100px;
  font-weight: 900;
  margin: 25px 0;
  text-shadow: 1px 5px rgb(0, 0, 0, 0.4);
}

.current .temp span {
  font-weight: 900;
  font-size: 80px;
  text-shadow: 2px 5px rgb(0, 0, 0, 0.4);
}

.current .weather {
  color: #fff;
  font-size: 32px;
  font-weight: 700;
  font-style: italic;
  margin-bottom: 15px;
  text-shadow: 0px 3px rgb(0, 0, 0, 0.4);
}

.current .hi-low {
  color: #ffff;
  font-size: 24px;
  font-weight: 500;
  text-shadow: 0 3px rgba(0, 0, 0, 0.4);
}

.forecast {
  background: grey;
}

table {
  width: 600px;
  color: #fff;
  text-shadow: 0 3px rgba(0, 0, 0, 0.4);
  border-radius: 4px;

  tr th,
  tr td {
    padding: 5px;
  }
}
